import React, { useEffect } from "react";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { Button } from "src/components";
import { host } from "src/LinkHost";

import * as S from "./styled";

export const TodayTime = () => {
  const navigate = useNavigate();
  const initialValues = {
    kor: "",
    eng: "",
    math: "",
    sci: "",
    com: "",
    kh: "",
    study: "",
  };
  useEffect(() => {
    if (!localStorage.getItem("accessToken")) {
      navigate("/");
      alert("   .");
    }
  }, []);
  const validationSchema = Yup.object().shape({
    kor: Yup.string().required(""),
    eng: Yup.string().required(""),
    math: Yup.string().required(""),
    sci: Yup.string().required(""),
    com: Yup.string().required(""),
    kh: Yup.string().required(""),
    study: Yup.string().required(""),
  });

  const onSubmit = (data) => {
    axios
      .post(`http://${host}:8080/todaytimes`, data, {
        headers: { accessToken: localStorage.getItem("accessToken") },
      })
      .then((response) => {
        navigate("/time/habit");
      });
  };
  return (
    <>
      <S.InputTimeContainer>
        <S.InputContainer>
          <Formik
            initialValues={initialValues}
            onSubmit={onSubmit}
            validationSchema={validationSchema}
          >
            <Form style={{ display: "flex", flexDirection: "column" }}>
              <label></label>
              <S.InputTime autoComplete="off" name="kor" />
              <label></label>
              <S.InputTime autoComplete="off" name="eng" />
              <label></label>
              <S.InputTime autoComplete="off" name="math" />
              <label></label>
              <S.InputTime autoComplete="off" name="sci" />
              <label></label>
              <S.InputTime autoComplete="off" name="com" />
              <label></label>
              <S.InputTime autoComplete="off" name="kh" />
              <label></label>
              <S.InputTime autoComplete="off" name="study" />
              <Button style={{ width: "13.5rem" }} type="submit">
                
              </Button>
            </Form>
          </Formik>
        </S.InputContainer>
      </S.InputTimeContainer>
    </>
  );
};
