import React, { useState, useEffect } from "react";
import axios from "axios";
import { host } from "src/LinkHost";
import { useMediaQuery } from "react-responsive";
import { useNavigate } from "react-router-dom";

import * as S from "./styled";

export const Intro = () => {
  const isDeskTop = useMediaQuery({ minWidth: "50rem" });
  const navigate = useNavigate();
  const [authState, setAuthState] = useState({
    id: 0,
    user_id: "",
    user_name: "",
    status: false,
  });

  const [teaState, setTeaState] = useState({
    t_job: "",
    status: false,
  });
  useEffect(() => {
    axios
      .get(`http://${host}:8080/auth/auth`, {
        headers: {
          accessToken: localStorage.getItem("accessToken"),
        },
      })
      .then((res) => {
        if (res.data.error) {
          setAuthState({ ...authState, status: false });
          setTeaState({ ...teaState, status: false });
        } else {
          setAuthState({
            id: res.data.id,
            user_id: res.data.user_id,
            user_name: res.data.user_name,
            status: true,
          });
          setTeaState({
            t_job: res.data.t_job,
            status: true,
          });
        }
      });
  }, []);
  /**
   * authState         
   * "  !"     
   *    .
   */
  const FuncLogout = () => {
    localStorage.removeItem("accessToken");
    setAuthState({
      user_id: "",
      user_name: "",
      status: false,
    });
  };
  const FuncLogin = () => {
    navigate("/auth/login");
  };
  const FuncRegister = () => {
    navigate("/auth/register");
  };
  return (
    <S.Background>
      {!authState.status ? (
        <div style={{ textAlign: "center", marginBottom: "5rem" }}>
            !
        </div>
      ) : (
        <></>
      )}
      <S.MainContainer>
        {!authState.status ? (
          <S.AuthContainer>
            <S.Flex>
              <S.Logout onClick={FuncLogin}>
                <S.AuthTitle></S.AuthTitle>
              </S.Logout>
              <S.Logout onClick={FuncRegister}>
                <S.AuthTitle></S.AuthTitle>
              </S.Logout>
            </S.Flex>
          </S.AuthContainer>
        ) : (
          <S.AuthContainer>
            <S.Flex>
              <S.auth>
                <S.AuthTitle style={{ marginTop: "0.2rem" }}>
                  {authState.user_name}
                </S.AuthTitle>
              </S.auth>
              <S.Logout onClick={FuncLogout}>
                <S.AuthTitle></S.AuthTitle>
              </S.Logout>
            </S.Flex>
          </S.AuthContainer>
        )}
        <S.SectionContainer>
          <S.SectionOne>
            <S.TitleContainer>
              <S.SectionTitle to="/time/today">
                  
              </S.SectionTitle>
              <S.explainContainer>
                <S.explainBox>
                     .
                  <br />   
                </S.explainBox>
              </S.explainContainer>
            </S.TitleContainer>
          </S.SectionOne>
        </S.SectionContainer>
        <S.SectionContainer>
          <S.SectionTwo>
            <S.TitleContainer>
              {authState.status === true ? (
                <S.SectionTitle to={`/myinfo/myplan/${authState.user_id}`}>
                  
                </S.SectionTitle>
              ) : (
                <S.SectionTitle to={`/`}></S.SectionTitle>
              )}

              <S.explainContainer>
                {isDeskTop ? (
                  <S.explainBox>
                       
                    <br />  ,      .
                  </S.explainBox>
                ) : (
                  <S.explainBox>
                       
                    <br />  ,    <br />  .
                  </S.explainBox>
                )}
              </S.explainContainer>
            </S.TitleContainer>
          </S.SectionTwo>
        </S.SectionContainer>
      </S.MainContainer>
    </S.Background>
  );
};
